<template>
  <page404
    :img-url="statePageConfig.imgUrl"
    :msg="statePageConfig.msg"
    :my-class="statePageConfig.myClass"
    :button-config="statePageConfig.buttonConfig"
  />
</template>

<script>
import page404 from 'src/components/exception/404-page/404-page.vue';

export default {
  components: {
    page404
  },
  data() {
    return {
      statePageConfig: {
        imgUrl: require('../../../public/images/404.png'),
        msg: {
          title: '404',
          content: '提示文字可自定义',
          notice: ''
        },
        myClass: 'myClass',
        buttonConfig: {
          name: '返回首页',
          callback: () => {
            this.$router.push({
              path: '/home'
            });
          }
        }
      }
    };
  },
  methods: {
    handTrigger() {
      this.$router.push({
        path: '/home'
      });
    }
  }
};
</script>



